
.main-news{
  .content__main{
    width: 100%;
  }
  width: 80%;
  float: left;
  .news-gallery-container{
    width: 80%;
    height: 338px;
    float: left;
    display: block;
    padding-right: 45px;
    box-sizing: border-box;
    position: relative;
    a{
      text-decoration: none;
      position: relative;
    }
    .big-news-gallery-item{
      display: block;
      width: 49%;
      float: left;
      height: 339px;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      &:hover{
        img{
          -webkit-transform: scale(1.04);
          -moz-transform: scale(1.04);
          -o-transform: scale(1.04);
          transform: scale(1.04);
          -webkit-transition: all ease-in-out 0.5s;
          -moz-transition: all ease-in-out 0.5s;
          -ms-transition: all ease-in-out 0.5s;
          -o-transition: all ease-in-out 0.5s;
          transition: all ease-in-out 0.5s;
        }
        .news-text{
          background-color: rgba(0,0,0,.5);
          -webkit-transition: all ease-in-out 0.5s;
          -moz-transition: all ease-in-out 0.5s;
          -ms-transition: all ease-in-out 0.5s;
          -o-transition: all ease-in-out 0.5s;
          transition: all ease-in-out 0.5s;

        }}
    }
    .news-gallery-item{
      position: relative;
      width: 25%;
      float: left;
      height: 169px;
      overflow: hidden;
      margin-left: 1px;
      margin-bottom: 1px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;

      }
      &:hover{
        img{
            -webkit-transform: scale(1.04);
            -moz-transform: scale(1.04);
            -o-transform: scale(1.04);
            transform: scale(1.04);
            -webkit-transition: all ease-in-out 0.5s;
            -moz-transition: all ease-in-out 0.5s;
            -ms-transition: all ease-in-out 0.5s;
            -o-transition: all ease-in-out 0.5s;
            transition: all ease-in-out 0.5s;
        }
        .news-text{
        background-color: rgba(0,0,0,.5);
        -webkit-transition: all ease-in-out 0.5s;
        -moz-transition: all ease-in-out 0.5s;
        -ms-transition: all ease-in-out 0.5s;
        -o-transition: all ease-in-out 0.5s;
        transition: all ease-in-out 0.5s;
      }}
    }
    .news-text{
      padding-left: 10px;
      padding-top: 10px;
      display: block;
      background-color: rgba(0,0,0,.2);
      position: absolute;
      min-height: 60px;
      bottom: 0;
      width: 100%;

      .gallery-news-text-header{
        font-family: $rr;
        font-size: 18px;
        color: #fff;
        margin: 0;
        margin-bottom: 8px;
      }
      p{
        color: #fff;
        font-family:$rr;
        font-size: 15px;
      }
    }
  .shape{
    width: 99.5%;
    height: 11px;
    margin-top: 11px;
    margin-bottom: 11px;

  }
  }
  .all-news-to-day{
    position: relative;
    width: 80%;
    float: left;
    display: block;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding-top: 40px;
    padding-bottom: 21px;
    .news{
      padding-left: 0;
    }
      h4{
        font-family: $rb;
        font-size: 16px;
        color: #000;
      }
    ul{
      display: block;
      width: 100%;
      float: left;
      list-style-type: none;
      .time{
        position: relative;
        top: 35px;
        left: -35px;
        display: block;
        width: 30px;
        text-align: center;
        font-size: 10px;
        span{
          width: 100%;
          font-size: 10px;
        }
      }
      a,span{
        position: relative;
        display: block;
        overflow: hidden;
        padding-right: 100px;
        height: 34px;
        color: #000;
        text-decoration: none;
        font-size: 13px;
        font-family: $rr;
        height: 32px;
        .views{
          position: absolute;
          top: 4px;
          right: -38px;
          width: 50px;
          color: #5b5b5b;
          font-size: 11px;
          font-family: $rr;
          .views-ico{
            position: relative;
            top: 0;
            margin-right: 4px;
            font-size: 14px;
            float: left;
          }
        }
      }
      .column-list{
        width: 50%;
        float: left;
        li{

          height: 46px;
        }
      }

    }
    .column-list{
      margin-bottom: 30px;
    }
    .paginator{
      width: 100%;
      display: inline-block;
      position: absolute;
      bottom: -15px;
      li{
        display: inline-block;
        width: 3%;
        a,span{
          width: 100%;
          display: inline-block;
          height: 100%;
          z-index: 5;
          position: relative;
          left: 0%;
        }
      }
      .active{
        a{
          color: #ff3432;
        }
      }
    }
  }
