.news-posts{
  width: 80%;
  position: relative;
  display: block;
  float: left;
  padding-top: 20px;
  padding-right: 30px;
  box-sizing: border-box;
  .more-news{
    list-style-type: none;
    li{
      position: relative;
        &::before{
          position: absolute;
          left: -20px;
          width: 4px;
          height: 4px;
          background-color: #ff3432;
          content: "";
          top: 7px;
    }
    }
  }

  &__item{
    //background-color: #e6e3e3;
    margin-right: 3px;
    margin-bottom: 3px;
    width: 49%;
    float: left;
    padding: 20px;
    box-sizing: border-box;
    //padding-left: 10%;
    position: relative;
    height: 390px;
    .post-overflow{
      overflow: hidden;
      height: 118px;
      background-color: #e6e3e3;
      padding-top: 10px;
      padding-right: 10px;
    }
    .date-news__post{
      width: 14%;
    }
    a{
      text-decoration: none;
    }
    .category{
      a{
      font-family:$rb;
      font-size: 16px;
      color: #000;
      }
    }
    .post-header{

      font-family:$rb;
      font-size: 14px;
        color: #000;
    }
    .post-image{
      height: 106px;
      width: 120px;
      overflow: hidden;
      float: left;
      margin: 0 10px;
      img{
        object-fit: cover;
        width: 100%;
        height:93%;
      }
    }
    .text-preview{
      font-family: $rr;
      font-size: 14px;
      color: #414a5a;
    }
    .more-news-link{
      text-decoration: none;
      font-family: $rr;
      font-size: 13px;
      color: #000;
    }
    .watch-all{
      position: absolute;
      bottom: 4px;
      color: #ff3432;
    }
    .read-more{
      color:#ff3432;
      position: relative;
      top: 10px;
    }
    .more-news{
      margin: 0;
      clear: both;
      padding: 20px;
    }
  }
.line{
  height: 1px;
  width: 75%;
  background-color: #e9e9e9;
  position: absolute;
  bottom: 0;
}
}
  .main-news-prefooter{
    width: 80%;
    float: left;
    display: block;
    padding: 28px 0;
    padding-bottom: 40px;
    .social{
      width: 50%;
      height: 100%;
      text-align: center;
      box-sizing: border-box;
      float: left;
      padding-bottom: 40px;
      .social-header{
        display: inline-block;
        font-family: $rb;
        font-size: 16px;
        color: #000;
        float: left;
        width:100%;
        text-align: center;
      }
      img{
        display: block;
        clear: both;
        margin: 0 auto;
      }
      #vk_groups_news{
        margin: 0 auto;
      }
    }
    .weather-forecast{
      float: right;
      width: 50%;
      height: 100%;
      box-sizing: border-box;
      .weather-header{
        display: inline-block;
        font-family: $rb;
        font-size: 16px;
        color: #000;
      }
      img{
        display: block;
        clear: both;
      }
    }
    .banner-bottom{
      margin: 35px 0;
      float: left;
      width: 98%;
      img{
        width: 100%;
      }
    }
  }

}
.img-zoom {
  width: 100%;
  -webkit-transition: all ease-in-out 0.2s;
  -moz-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
  transition: all ease-in-out 0.2s;
}
.transition {

}
.datepicker-inline .datepicker{
  float: left;
  width: 100%;
}

.list-view{
  float: left;
  width: 100%;
  margin-left: -10px;
  box-sizing: border-box;
}

.date-picker-header{
  border: solid 1px #e6e3dc;
  background-color: #fff;
  display: block;
  float: left;
  width:100%;
  border-top: 5px solid #ff3432;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  height: 34px;
  font-family: $rb;
  font-size: 13px;
  color: #0f0101;
  padding-top: 6px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.another-news{
  width: 100%;
  float: left;
  position: relative;
  border-top: -20px;
  .rand-cat-news, .best-views-news{
    width: 100%;
    float: left;
    position: relative;
  }
  h3{
    font-family:$rb;
    font-size: 15px;
  }
  .news-like-item{
    width: 33.3%;
    float: left;
    position: relative;
    overflow: hidden;
    color: #242424;
    text-decoration: none;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding-right: 20px;
    margin-bottom:25px;
    height: 135px;

    &:hover{
      text-decoration: none;
    }
    .news-like-img{
      width:30%;
      float: left;
      height:auto;
      margin-right: 10px;
      img{
        width: 100%;
        height:100%;
        object-fit: cover;
      }
    }
    .new-header{
      font-family:$rb;
      font-size: 13px;
      overflow: hidden;
      height: 41px;
    }
    p{
      font-family:$rr;
      font-size: 12px;
    }
  }
}